	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Same Applies to Bayern München","date":"21 Oct 2004","slug":"/2004-10-21-same-applies-to-bayern-mnchen/","tags":"Y2004"},"html":"<p><a href=\"http://www.washingtonmonthly.com/archives/individual/2004_10/004964.php#304555\" title=\"The Washington Monthly\">Comment in Political Animal:</a> \"I really really really hate the Yankees. More than brussel sprouts. More than wet socks. More than ice cream headaches. A lot.\"</p>"},"next":{"fields":{"title":"Liquidity Squeeze","date":"21 Oct 2004","slug":"/2004-10-21-liquidity-squeeze/"}},"previous":{"fields":{"title":"If They Come to Protect You, Run","date":"22 Oct 2004","slug":"/2004-10-22-if-they-come-to-protect-you-run/"}}},{"node":{"fields":{"title":"Liquidity Squeeze","date":"21 Oct 2004","slug":"/2004-10-21-liquidity-squeeze/","tags":"Y2004"},"html":"<p>Last night my employer <a href=\"http://www.dgap.de/psf/content.php?isin=&a=20&v=de&b=db&l=1&von=20041020&bis=20041020&emittent=&land=DE&typ=adhoc&Jahreswahl=&SVON=&vont=20&vonm=10&vonj=2004&SBIS=&bist=20&bism=10&bisj=2004&suche=\" title=\"\">issued</a> an Ad-Hoc Release that we are suffering from a <a href=\"http://dict.leo.org/?search=liquidit%E4tsengpass\" title=\"\"><i>Liquidit&auml;tsengpass</i></a>. In English, I guess that means we won't be seeing our salaries anytime soon. No news to us, but it was news to the stock market.</p>"},"next":{"fields":{"title":"Enough with the US election","date":"19 Oct 2004","slug":"/2004-10-19-enough-with-the-us-election/"}},"previous":{"fields":{"title":"Same Applies to Bayern München","date":"21 Oct 2004","slug":"/2004-10-21-same-applies-to-bayern-mnchen/"}}},{"node":{"fields":{"title":"Enough with the US election","date":"19 Oct 2004","slug":"/2004-10-19-enough-with-the-us-election/","tags":"Y2004"},"html":"<p><a title=\"Bjørn Stærk blog - Enough with the US election\" href=\"http://blog.bearstrong.net/001470.html\">Bjørn Stærk: Enough with the US election</a> \"You get a sense of how intertwined European and American culture has<br />\nbecome, and how much of a one-way relationship it is, when you look at<br />\nthe amount of press coverage the US election receives here. It may seem<br />\nobvious to my American readers that their election should be covered in<br />\nfull detail all across the world, but it's not. You're not <i>that</i> important.  But we care anyway... On October 18, Norway's online newspapers published 52 articles mentioning John Kerry. On the same day, they published 18 articles mentioning our own prime minister Kjell Magne Bondevik.\" It's just as bad in Germany.</p>"},"next":{"fields":{"title":"Dead-End Job","date":"19 Oct 2004","slug":"/2004-10-19-deadend-job/"}},"previous":{"fields":{"title":"Liquidity Squeeze","date":"21 Oct 2004","slug":"/2004-10-21-liquidity-squeeze/"}}}],"pathPrefix":"","first":false,"last":false,"index":399,"pageCount":1101}}

